
import Alert from 'vux/src/components/alert/index.vue'
import Msg from 'vux/src/components/msg/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

import AlertPlugin from 'vux/src/plugins/alert/index.js'

import Vue from 'vue'
const pulldownDefaultConfig = {
content: '下拉刷新',
height: 40,
autoRefresh: false,
downContent: '下拉刷新',
upContent: '释放后刷新',
loadingContent: '正在刷新...',
clsPrefix: 'xs-plugin-pulldown-'
}
const pullupDefaultConfig = {
content: '上拉加载更多',
pullUpHeight: 60,
height: 40,
autoRefresh: false,
downContent: '释放后加载',
upContent: '上拉加载更多',
loadingContent: '加载中...',
clsPrefix: 'xs-plugin-pullup-'
}
Vue.use(AlertPlugin)

export default {
components: {
alert,Msg,Badge,Scroller
},
data(){
return {
list: [],
pullupDefaultConfig: pullupDefaultConfig,
pulldownDefaultConfig: pulldownDefaultConfig
}
},
methods:{

jump(id){
this.$router.push('/');
},
refresh() {
console.log("refresh");

},
loadMore() {
console.log("loadMore");

}
},
mounted: function(){
console.log("created");
this.$nextTick(() => {
this.$refs.scrollerBottom.reset({top: 0})
})
}

}




